<template>
  <article-layout def :title="title" :time="time" :heat="heat">
    <div>
      <div style="color: #808695;font-size: smaller;padding: 10px 0">
        原文地址：
        <a :href="linkSrc" target="_blank">{{linkSrc}}</a>
      </div>
      <div v-for="(item, index) in article" :key="index">
        <template v-if="item.type==='text'">
          <div class="p-div">{{item.content}}</div>
        </template>
        <template v-else-if="item.type==='image'">
          <div style="margin: 10px;text-align: center">
            <img style="max-width: 100%" :src="item.content" alt="">
          </div>
        </template>
      </div>
    </div>
  </article-layout>
</template>

<script>
  import ArticleLayout from "@/components/layout/ArticleLayout"

  export default {
    name: "InterestingArticle1",
    components: {
      ArticleLayout
    },
    data() {
      return {
        title: '刘禅追谥尚欠赵云一个正点',
        time: '2020-04-01',
        heat: '1.2k',
        linkSrc: 'http://www.e3ol.com/culture/html/2016-10/25941/25941_20161028.shtml',
        article: [
          {
            type: 'text',
            content: '君主时代帝王死后，会依据他生前的所作所为给他一个定性类的称号，' +
              '对于一些重要的大臣来说，也会得到这样的待遇，称之为谥号。这是一种盖棺论' +
              '定，尤其对于大臣，能获得谥号表明位高权重或者是功劳大，所以，这是一种身' +
              '后的荣耀。蜀汉国实际建国时间相对较晚还短，获得谥号的大臣只有六人，后来' +
              '刘禅又追谥了几人，前后总共不过十几个人。赵云就是这几个被追谥的大臣之一。'
          },
          {
            type: 'text',
            content: '按理说，能够被皇帝追谥享受哀荣，也不应该有什么可说的吧？但问题' +
              '在于，赵云被追谥，是在关羽、张飞、马超、庞统、黄忠五人之后的第二年，这' +
              '就不免让人有些不解。'
          },
          {
            type: 'text',
            content: '刘禅是在景耀三年（公元260）九月给以上五人追谥的，第二年的三月，' +
              '又单独给赵云追谥。《云别传》记载追谥过程是：刘禅下诏说：“赵云很早跟随先' +
              '帝（刘备），功劳显著。朕以幼冲之年，经历了路途艰难，依仗着（你的）忠顺，' +
              '这才脱离了危险。谥号是用来评议国家元老功臣的，你们议论一下，赵云应该用一' +
              '个什么谥号。”大将军姜维等人经过议论，认为赵云过去跟随先帝，功劳显著，在' +
              '跟随先帝打天下的过程中，遵守臣子本分，功劳和付出都是可以载于史册的。当阳' +
              '长坂坡的战役中，赵云的行为义举，比金石还要贵重。以自己的忠心护卫主上，君' +
              '主想到是如何奖赏；以礼节和厚赏对待属下，臣子必然是舍生忘死。（看到追谥的' +
              '诏书）赵云如果地下有知，足可以感觉到自己的功劳没有湮灭；生者感动于主上的' +
              '恩德，一定会以生命来报答。于是按照谥法，应该追谥赵云为顺平侯。'
          },
          {
            type: 'text',
            content: '诏书和评议对赵云的定性和给予的谥号都没有什么问题，问题在于，赵云' +
              '为什么不能和以上五人同时追谥？虽然有句话叫做“好饭不怕晚”，但到了赵云这儿，' +
              '是不是也太晚了点！这给人最大的不解就是，赵云这个谥号好像是忘记了，让人提醒' +
              '又想起来，只好再补上似的。'
          },
          {
            type: 'text',
            content: '君主时代是官本位社会，一个人的能力越大、功绩越大其官位就越高。关、' +
              '张、马、黄是刘备时期的前后左右四大将，赵云的官职爵位不能和他们比，也就不说' +
              '了，另外还有一个庞统，赵云和他相比是个什么情况呢？庞统也是军师中郎将，和诸' +
              '葛亮同一种官名，只是刘备对他的重视程度比诸葛亮稍弱一点点。功劳方面，跟随刘' +
              '备入蜀，刘备所用计策多为庞统所出，虽然在围攻雒城战役中中箭身亡，但这时候的' +
