              '，庞统似乎要重于赵云。追记已故大臣的功绩，应该会和他同时期的人有个比较衡量' +
              '，所以，刘禅延续刘备的看法和已定成规，这个本就不足为奇。但问题是，当下的这' +
              '个皇帝是刘禅，是赵云两次救过他命的人！所以这就要看刘禅的态度了。'
          },
          {
            type: 'text',
            content: '刘禅当皇帝期间，诸葛亮、蒋琬、费祎去世后也得到了谥号，《三国志》说' +
              '他们是“荷国之重”，这同样不需要再多说什么。关键在于，“陈祗宠待，特加殊荣”，' +
              '而赵云为什么就不能特加殊荣？陈祗在《三国志》当中有传，这让我们能够对他有一' +
              '个大概的了解。陈祗二十岁时就有了名气，慢慢升职为选曹郎，仪容举止庄重严厉。' +
              '他多才多艺，天文、历法、占卜之类的学问都很有造诣。董允死后，陈祗被越级提拔' +
              '为内侍之职。吕乂死后，陈祗又以侍中身份代理尚书令，加官镇军将军。那么，陈祗' +
              '都干了一些什么事情呢？“对上逢迎皇上所好，对下勾结宦官”，因此得到了皇帝刘禅' +
              '的信任和喜爱。再其他的就是勾结宦官，献媚皇上。因此说，我们能对此人知道一个' +
              '大概，但就是看不到他到底有什么功劳。就是这样一个人，在景耀元年去世后，刘禅' +
              '感到痛惜，除了流泪不止，还下诏书“特赠他谥号为忠侯”。看看吧，一个救命的不如' +
              '一个溜须拍马逢迎献媚的，这就是蜀汉国的第二位皇帝刘禅。'
          },
          {
            type: 'text',
            content: '大凡无能的皇帝有一个共同的特点，信任宦官，刘禅是否就是这样一个皇帝？'
          },
          {
            type: 'text',
            content: '另外还想到了汉初的夏侯婴。彭城兵败，夏侯婴驾车拉着刘邦急速逃跑，半' +
              '路上遇到了刘邦的一儿一女，夏侯婴把他们收上了车。敌人在后边紧追不舍，刘邦本' +
              '来就嫌自己跑得还不够快，马已经十分疲劳，再加上两个人，这车子就更显得慢，刘' +
              '邦心里的气肯定是不打一处来！于是就有了以下情景：刘邦把两个孩子踢下车，夏侯' +
              '婴再把他们收上来，一连重复多次，直到两个孩子抱紧了夏侯婴的脖子。当时的刘邦' +
              '是生气的，有好几次刘邦甚至想杀了夏侯婴。这和长坂坡赵云救他刘禅有相似之处。' +
              '赵云寻找刘禅，刘备是失态的，因为他用手戟掷向那个告诉他赵云向北走了的人。这' +
              '说明他认为当时他们一行人的安全才是重要的，而在这个时候赵云是不应该离开自己' +
              '单独行动的。夏侯婴救得这个男孩后来当了皇帝，就是汉惠帝刘盈。刘盈继位后对夏' +
              '侯婴做了什么呢？把近靠皇宫的一处第一等的宅子赐给了他！还起了个名字叫做“近' +
              '我”，意思是“这样可以离我最近”，以表示特别的恩宠。汉朝的事情常常被三国帝王' +
              '提及，刘禅难道就不能效法一下汉惠帝吗？'
          },
          {
            type: 'text',
            content: '赵云的谥号为什么来得那么晚？恐怕还真是有点儿被刘禅遗忘了。只不过是' +
              '刘禅自己最终良心发现还是真有人提醒，我们现在已经无从得知了。所以说，尽管刘' +
              '禅给赵云追加了谥号，但还是欠赵云一个正点。'
          }
        ]
      }
    }
  }
</script>

<style scoped>
  .p-div {
    text-indent: 36px;
    line-height: 36px;
  }
</style>